.container {
  display: grid;
  grid-template-areas:
  "header header"
  "nav main";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 0px 20px;
  background-color: #182636;
  /*height: 100vh;*/
}
.main {
  grid-area: main;
}
